<template>
  <div class="row">
    <el-tabs class="full-width"
             v-model="activeName"
    >
      <el-tab-pane
        label="Chi tiết khách hàng"
        name="detail"
      >
        <div class="card">
          <div class="card-header">
            <h6 class="title">Chi tiết khách hàng</h6>
          </div>
          <div class="card-body form-card">
            <div class="row">
              <div class="col-sm-12 p-0">
                <label>Họ tên: </label>
              </div>
              <div class="col-sm-12 p-0">
                <el-input
                  v-model="form.name"
                  :class="errors.has('name')?'border-danger':''"
                  v-validate="'required'"
                  data-vv-name="name"
                  data-vv-as="Tên"
                ></el-input>
                <span class="text-danger" v-if="errors.has('name')">{{ errors.first('name') }}</span>
              </div>
            </div>
            <div class="row">
              <div class="col-sm-12 p-0">
                <label>Email: </label>
              </div>
              <div class="col-sm-12 p-0">
                <el-input
                  v-model="form.email"
                  :class="errors.has('email')?'border-danger':''"
                  v-validate="'required|email'"
                  data-vv-name="email"
                  data-vv-as="Email"
                ></el-input>
                <span class="text-danger" v-if="errors.has('email')">{{ errors.first('email') }}</span>
              </div>
            </div>
            <div class="row">
              <div class="col-sm-12 p-0">
                <label>Số điện thoại: </label>
              </div>
              <div class="col-sm-12 p-0">
                <el-input v-model="form.phone"></el-input>
              </div>
            </div>
            <div class="row">
              <div class="col-sm-12 p-0">
                <label>Địa chỉ: </label>
              </div>
              <div class="col-sm-12 p-0">
                <el-input v-model="form.address"></el-input>
              </div>
            </div>
            <div class="row">
              <div class="col-sm-12 p-0">
                <label>Tỉnh/Thành phố: </label>
              </div>
              <div class="col-sm-12 p-0">
                <my-region v-model="form.dataRegion" :regionID="form.region" :subRegionID="form.subregion"></my-region>
              </div>
            </div>
          </div>
        </div>
      </el-tab-pane>
      <el-tab-pane
        label="Danh sách đơn hàng"
        name="orders"
      >
        <div class="col-md-12 card p-0">
          <div class="card-body row p-0">
            <div class="col-sm-12">
              <my-table :columnDefs="columnDefs" v-bind:data-rows="customerOrders" :actions="actions"/>
            </div>
          </div>
        </div>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>
<script>
  import {Select, Option, Button, Tabs, TabPane, Input, Message} from 'element-ui'
  import MyTable from 'src/components/UIComponents/Table.vue'
  import MyFilter from 'src/components/UIComponents/Filter.vue'
  import MyRegion from 'src/components/UIComponents/Region.vue'
  import dtHelper from 'src/helpers/datatable'
  import { mapState } from 'vuex'
  import ordersSchemas from './customer-order-schemas'

  export default{
    components: {
      ElSelect: Select,
      ElOption: Option,
      ELButton: Button,
      ElTabs: Tabs,
      ElTabPane: TabPane,
      ElInput: Input,
      MyTable,
      MyFilter,
      MyRegion,
      Message
    },
    data() {
      return {
        activeName: 'detail',
        form: {},
        columnDefs: dtHelper.buildColumDefs(ordersSchemas),
        actions: [
          {
            type: 'primary',
            icon: 'nc-icon nc-ruler-pencil',
            callback: this.orderDetail
          }
        ],
      }
    },
    computed: mapState(['customerDetail', 'customerOrders']),
    mounted() {
      let id = this.$route.params.id;
      this.$store.dispatch('fetchCustomerDetail', id);

      this.$store.dispatch('setPageTitle', 'chỉnh sửa khách hàng');
      this.$store.dispatch('setCurrentActions', [{
        label: 'Cập nhật',
        type: 'primary',
        icon: '',
        callback: this.save
      }])
    },
    methods: {
      orderDetail(index, row){
        this.$router.push('/orders/' + row.id);
      },
      save(){
        let self = this;
        self.form.region = self.form.dataRegion.region;
        self.form.subregion = self.form.dataRegion.subRegion;
        this.$validator.validateAll().then(function (result) {
          if (result) {
            self.$store.dispatch('updateCustomer', self.form).then(res => {
              Message({
                message: 'Cập nhật thành công',
                type: 'success'
              });
            }, res => {
              Message({
                message: res.message,
                type: 'error'
              });
            });
          }
        })
      }
    },
    watch: {
      customerDetail(newValue, oldValue) {
        if (!newValue.region) newValue.region = '';
        this.form = newValue
      }
    },
    destroyed(){
      this.$store.dispatch('setCurrentActions', []);
    }
  }
</script>
<style lang="scss">
  .el-table .td-actions{
  button.btn {
    margin-right: 5px;
  }
  }
</style>
